*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-color: #84a178;
  --second-color: #fcfcfc;
  --additional-color: #131313;
  --accent-link-color: #f8d24e;
  --focus-color: #0a8817;
  --disabled-color: #ababab;
  --main-font: "Montserrat", sans-serif;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  opacity: 0;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--main-font);
  color: var(--second-color);
  background-color: var(--primary-color);
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.btn {
  text-align: center;
  color: #000;
  background: var(--accent-link-color);
  cursor: pointer;
  border: 0;
  border-radius: 10px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.page-header {
  width: 100%;
  z-index: 5;
  background: none;
  justify-content: start;
  padding-top: 8px;
  padding-left: 16px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.container-header {
  display: flex;
  position: relative;
}

.header-site-nav {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  position: absolute;
  bottom: 0;
  left: 300px;
}

@media screen and (max-width: 1199.99px) {
  .header-site-nav {
    display: none;
  }
}

.header-site-nav-text {
  color: var(--second-color);
  cursor: url("Cursor.c24f6361.png"), pointer;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.header-site-nav-text:not(:last-child) {
  margin-right: 28px;
}

.header-site-nav-text:hover, .header-site-nav-text:focus {
  color: var(--accent-link-color);
}

.lang-select {
  position: absolute;
  bottom: 0;
  right: 180px;
}

.change-lang {
  text-align: center;
  color: #000;
  background: var(--accent-link-color);
  cursor: auto;
  border: 0;
  border-radius: 10px;
  padding: 10px 20px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.change-lang-link {
  text-align: center;
  color: #000;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.change-lang-text {
  text-align: center;
  color: #000;
  cursor: pointer;
  margin: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.btn-header {
  height: 42px;
  width: 152px;
  text-align: center;
  cursor: url("Cursor.c24f6361.png"), pointer;
  justify-content: center;
  padding: 10px 20px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.16;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  bottom: 0;
  right: 16px;
}

.btn-header:hover {
  background-color: #0a8817;
}

.btn-header:focus {
  color: var(--second-color);
  background-color: #0a8817;
}

.btn-header:disabled {
  background-color: #ababab;
}

.container-hero {
  height: 502px;
  background: var(--primary-color);
  margin: 0 auto;
  position: relative;
  top: 0;
  left: 0;
}

@media screen and (min-width: 480px) {
