  background: none;
  justify-content: center;
  margin-right: 12px;
  padding: 11px 26px;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.25;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-hero:hover {
  border: 2px solid #0a8817;
}

.btn-hero:focus {
  color: var(--second-color);
  border: 2px solid #0a8817;
}

.btn-hero:disabled {
  border: 2px solid #ababab;
}

@media screen and (min-width: 768px) {
  .btn-hero {
    margin-right: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .btn-hero {
    margin-right: 34px;
    padding: 12px 34px;
    font-size: 14px;
    line-height: 1.16;
  }
}

.hero-container-link {
  color: var(--accent-link-color);
  text-align: center;
  align-items: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.25;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero-container-link:hover {
  color: #0a8817;
}

.hero-container-link:focus {
  color: var(--second-color);
}

.hero-container-link:disabled {
  color: #ababab;
}

@media screen and (min-width: 1200px) {
  .hero-container-link {
    font-size: 14px;
    line-height: 1.21;
  }
}

.hero-bubbles-wrapper {
  width: 100%;
  height: 502px;
  position: relative;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  .hero-bubbles-wrapper {
    height: 415px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-bubbles-wrapper {
    height: 665px;
  }
}

.bubbles {
  z-index: 3;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 320px) {
  .bubbles .point {
    width: 5px;
    height: 5px;
    animation: animate-bubbles 5000s ease-in-out infinite reverse;
    animation-duration: calc(50s / var(--i) );
    background: #f8d24e;
    border-radius: 50%;
    margin: 0 1px;
    position: relative;
    box-shadow: 0 0 0 2px rgba(248, 210, 78, .267), 0 0 15px rgba(248, 210, 78, .467), 0 0 40px #f8d24e;
  }

  .bubbles .point:nth-child(2n) {
    background: #324db8;
    box-shadow: 0 0 0 4px rgba(50, 77, 184, .267), 0 0 50px rgba(50, 77, 184, .467), 0 0 100px #324db8;
  }
}

@media screen and (min-width: 480px) {
  .bubbles .point {
    width: 6px;
    height: 6px;
    box-shadow: 0 0 0 4px rgba(248, 210, 78, .267), 0 0 20px rgba(248, 210, 78, .467), 0 0 50px #f8d24e;
  }
}

@media screen and (min-width: 768px) {
  .bubbles .point {
    width: 8px;
    height: 8px;
    box-shadow: 0 0 0 5px rgba(248, 210, 78, .267), 0 0 25px rgba(248, 210, 78, .467), 0 0 60px #f8d24e;
  }
}

@media screen and (min-width: 1200px) {
  .bubbles .point {
    width: 12px;
    height: 12px;
    margin: 0 2px;
    box-shadow: 0 0 0 5px rgba(248, 210, 78, .267), 0 0 50px rgba(248, 210, 78, .467), 0 0 100px #f8d24e;
  }
}

.hero-snowflake, .hero-snowflake-even {
  width: 5px;
  height: 5px;
  fill: #324db8;
  transform: translateY(-175%);
}

.hero-snowflake-even {
  fill: var(--accent-link-color);
}

@media screen and (max-width: 320px) {
  .hero-snowflake, .hero-snowflake-even {
    display: none;
  }
}

@media screen and (min-width: 480px) {
  .hero-snowflake, .hero-snowflake-even {
    width: 6px;
    height: 6px;
    transform: translateY(-130%);
  }
}

@media screen and (min-width: 768px) {
  .hero-snowflake, .hero-snowflake-even {
    width: 8px;
    height: 8px;
    transform: translateY(-75%);
  }
}

@media screen and (min-width: 1200px) {
  .hero-snowflake, .hero-snowflake-even {
    width: 12px;
    height: 12px;
    transform: translateY(-15%);
  }
}

@media screen and (min-width: 320px) {
  @keyframes animate-bubbles {
    0% {
      transform: translateY(502px)scale(1);
    }

    100% {
      transform: translateY(10vh)scale(0);
    }
  }
}

@media screen and (min-width: 480px) {
  @keyframes animate-bubbles {
    0% {
      transform: translateY(415px)scale(1);
    }

    100% {
      transform: translateY(15vh)scale(0);
    }
  }
}

@media screen and (min-width: 1200px) {
  @keyframes animate-bubbles {
    0% {
      transform: translateY(665px)scale(1);
    }

    100% {
      transform: translateY(20vh)scale(0);
    }
  }
}

@media screen and (max-width: 767px) {
  .hero-readmore {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .hero-readmore {
    width: 195px;
    color: var(--second-color);
    z-index: 5;
    position: absolute;
    top: 250px;
    left: 170px;
  }

  .hero-readmore .hero-readmore-input {
    opacity: 0;
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 250px;
  }

  .hero-readmore .hero-readmore-text {
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    font-size: 11px;
    line-height: 1.4;
    display: -webkit-box;
    overflow: hidden;
  }

  .hero-readmore .hero-readmore-input:focus ~ .hero-readmore-label {
    outline: -webkit-focus-ring-color auto 5px;
  }

  .hero-readmore .hero-readmore-input:checked + .hero-readmore-text {
    -webkit-line-clamp: unset;
  }

  .hero-readmore .hero-readmore-input:checked ~ .hero-readmore-label {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .hero-readmore {
    width: 210px;
    top: 440px;
    left: 250px;
  }

  .hero-readmore .hero-readmore-input {
    left: 250px;
  }

  .hero-readmore .hero-readmore-text {
    font-size: 13px;
    line-height: 1.4;
  }
}

.hero-readmore-label {
  margin-top: 10px;
  font-size: 35px;
  font-weight: 400;
}

@media screen and (max-width: 767px) {
  .hero-ice-creams {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .hero-ice-creams {
    z-index: 5;
    animation: animate-icecreams 50s ease-in-out infinite;
    display: block;
    position: absolute;
    bottom: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-ice-creams {
    bottom: 65px;
  }
}

@keyframes animate-icecreams {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(-2.5turn);
  }

  100% {
    transform: rotate(2.5turn);
  }
}

.hero-ice-cream {
  z-index: 5;
  position: absolute;
  bottom: 0;
  right: 40px;
}

@media screen and (min-width: 480px) {
  .hero-ice-cream {
    right: 0;
  }
}

@media screen and (min-width: 768px) {
  .hero-ice-cream {
    right: 75px;
  }
}

@media screen and (max-width: 320px) {
  .hero-ice-cream-eyes {
    display: none;
  }
}

@media screen and (min-width: 320px) {
  .hero-ice-cream-eyes {
    opacity: 1;
    z-index: 5;
    animation: animate-eyes 10s ease-in-out 1s infinite reverse;
    position: absolute;
    bottom: 111px;
    right: 142px;
  }
}

@media screen and (min-width: 480px) {
  .hero-ice-cream-eyes {
    bottom: 138px;
    right: 126px;
  }
}

@media screen and (min-width: 768px) {
  .hero-ice-cream-eyes {
    bottom: 170px;
    right: 230px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-ice-cream-eyes {
    bottom: 290px;
    right: 337px;
  }
}

@keyframes animate-eyes {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  81% {
    opacity: 0;
  }

  87% {
    opacity: .1;
  }

  90% {
    opacity: 1;
  }

  91% {
    opacity: 0;
  }

  97% {
    opacity: .1;
  }

  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 767px) {
